.global-nav {           
    a {
		@include rr-font-compute(r,medium);
		font-weight: 500;
		transition: opacity .3s ease-in-out;

		&:last-child {
			border-bottom-width: 0;
		}
        &:link, &:visited {
			//color: color(wildcat-white);
			text-decoration: none;
            opacity: 0.85;
        }
        &:hover, &:active {
			//color: color(wildcat-white);
			opacity: 1;            
            text-decoration: underline;
		}		
	}

	@media screen and (max-width: #{rr-prop-for-breakpoint(l,size)}) {
		.ic:before {
			font-size: .9em;
		}
		a {
			padding: rr-gridbase(1);
			border-bottom: 1px rgba(0,0,0,.1) solid;
			display: block;
			.show-for-sr {
				position: static;
				display: inline;
				margin-left: .55em;
			}
		}
	}

	@include rr-break-directive(l) {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		a {
			margin-right: rr-gridbase(4);
		}	
	}
	
	@include rr-break-directive(xl) {
		a {
			margin-right: rr-gridbase(6);
			@include rr-font-compute(l,medium);
		}
	}
}

.global-nav-bar {
	display: none;

	@include rr-break-directive(l) {
		display: block;
	}
}
