@import 'global/colors';
@import 'global/font-styles';
@import 'global/placeholder';
@import 'global/uky-logo-main';


body {
	margin: 0;
	padding: 0;

	@include usescheme($text-colors);
}

.default-colors {
	@include usescheme($text-colors);
}

.dark-colors {
	@include usescheme($text-colors);
}

// Prep images to be responsive
img {
	width: 100%;
	height: auto;
	display: block;
}

hr {
	border-width: 0 0 1px;
	padding-bottom: rr-grid-remainder(1px);
}

// Hide component from sighted users,
// maintain for screen readers

// show-for-sr is used in most case,
// `invisible` is used by the modal JS library
.show-for-sr, 
.invisible {
	display: block;
	position: absolute;
	top: -999999em;
	left: -999999em;
}

.show-for-none {
	display: none;
}

form {
	label,
	input,
	select,
	textarea {
		@include rr-font-compute(l,medium);
		font-weight: 500;
	}

	input[type=text],
	select {
		border: 1px color(wildcat-blue) solid;
		background-color: color(wildcat-white);
		border-radius: 0;
	}

	[type=submit] {
		text-align: right;
		margin: .5rem 0 .5rem .5rem;
	}

	.form-item {
		margin-bottom: rr-gridbase(2);
		label {
			margin-right: 1rem;
		}
	}

	.form-item {

	}
}